import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";

//import completedStyle from "assets/jss/material-kit-react/views/componentsSections/completedStyle.jsx";
import productStyle from "assets/jss/material-kit-react/views/landingPageSections/productStyle.jsx";

class SectionHome extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.section}>
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={8}>
              <h2 className={classes.title}>MIELE E PRODOTTI DELL'ALVEARE</h2>
              <h4 className={classes.description}>
                Il nostro miele viene estratto manualmente e non viene sottoposto a nessun altro tipo di lavorazione, per questo cristallizza naturalmente.

                I nostri prodotti trasmettono i profumi tipici dei nostri territori irpini, a partire dal miele di sulla, variet floreale primaria della nostra zona caratterizzata da ampi pascoli e terreni seminati per fienaggione, al castagno prodotto dalle fioriture degli alberi caratteristici delle nostre zone.
              </h4>
            </GridItem>
            <GridItem xs={12} sm={12} md={8}>
              <h2 className={classes.title}>LA CRISTALLIZZAZIONE DEL MIELE: un fenomeno naturale</h2>
              <h4 className={classes.description}>
              La cristallizzazione  un processo naturale che interessa il miele ed  un segno della sua genuinit.

              La cristallizzazione  legata alla alla composizione chimica del miele, e ogni tipologia di miele ha diversi tempi e modalit di 
              cristallizzazione.<br />
              
              La scelta tra liquido e cristallizzato  una questione di preferenze personali; se per si vogliono privilegiare i prodotti pi integri 
              bisogna ricordare che, al di fuori della stagione produttiva, i soli mieli che si mantengono stabilmente liquidi sono lacacia, 
              il castagno e le melate. Negli altri la presentazione allo stato liquido dipende da un trattamento termico di rifusione ed  noto 
              che il riscaldamento produce una perdita di alcune caratteristiche naturali del miele.<br />.
              
              Il miele cristallizzato acquistato pu essere riportato eventualmente allo stato liquido per mezzo di un opportuno riscaldamento (a bagnomaria a temperatura non superiore a 40 C).
              Ma pu essere ancora pi interessante abituarsi alluso di miele cristallizzato: si scoprir allora che questa forma fisica rende forse
               il miele meno comodo da prelevare e da sciogliere, ma molto pi facile da maneggiare, in quanto non cola, e molto pi piacevole da
                consumare poich viene percepito meno dolce e pi fresco al palato.
              </h4>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withStyles(productStyle)(SectionHome);
